import { CreatedUserId, ImageId, ImagePath, ProjectId } from '../value';

export class Image {
  readonly imageId: ImageId;
  readonly imagePath: ImagePath;
  readonly projectId: ProjectId;
  readonly createdUserId: CreatedUserId;

  constructor(args: {
    imageId: ImageId;
    imagePath: ImagePath;
    projectId: ProjectId;
    createdUserId: CreatedUserId;
  }) {
    this.imageId = args.imageId;
    this.imagePath = args.imagePath;
    this.projectId = args.projectId;
    this.createdUserId = args.createdUserId;
  }
}
